import { Component } from '@angular/core';
import { QRScanner, QRScannerStatus } from '@ionic-native/qr-scanner';
import { IonicPage, NavController, NavParams, Platform, ToastController, LoadingController } from 'ionic-angular';
import { HttpServicesProvider } from '../../providers/http-services';
import { SqliteProvider } from '../../providers/sqlite';
import { StorageProvider } from '../../providers/storage';
// import { Html5Qrcode } from "html5-qrcode";
declare var Html5Qrcode: any;

@IonicPage()
@Component({
  selector: 'page-scanner',
  templateUrl: 'scanner.html',
})
export class ScannerPage {
  public lightFlag: boolean = false;
  public type: string = '';
  public taskid: string = '';
  public offlinepi;
  public qrCode;
  public isBrowser: boolean = false;
  public loading: any = null;

  constructor(public navCtrl: NavController,
    public navParams: NavParams,
    public qrscanner: QRScanner,
    public platform: Platform,
    public storage: StorageProvider,
    public sqlite: SqliteProvider,
    public toastCtrl: ToastController,
    public loadingCtrl: LoadingController,
    public httpService: HttpServicesProvider) {
    this.offlinepi = this.storage.get(StorageProvider.OFFLINE_PI);
  }
  ionViewWillEnter() {
    this.platform.registerBackButtonAction(() => {
      this.navCtrl.popToRoot();
    })
    this.lightFlag = false;

    // 判断是不是浏览器环境
    this.isBrowser = this.storage.get(StorageProvider.IS_BROWSER);
    if (this.isBrowser) {
      console.log('== 浏览器H5扫码 ==');
      this.browserQRScanner();
    } else {
      console.log('== APP插件扫码 ==');
      this.qrscanner.destroy();
      this.scanQRScanner();
    }

    this.type = this.navParams.get('type');
    this.taskid = this.navParams.get('taskid');
  }
  ionViewDidLeave() {
    if (this.isBrowser) {
      this.onScanStop();
    } else {
      this.qrscanner.hide();
      this.qrscanner.destroy();
    }
  }
  //回退按钮
  back() {
    this.navCtrl.pop();
  }
  //调用插件开始扫码
  scanQRScanner() {
    if (typeof this.qrscanner != undefined) {
      this.qrscanner.prepare().then((status: QRScannerStatus) => {
        if (status.authorized) {
          // alert('成功授权');
          let scanSub = this.qrscanner.scan().subscribe((text: string) => {
            // alert(text);
            console.log('scanQRScanner扫描获取的数据：', text, ';扫码类型code：', this.type);

            if (this.type === 'leasset') {//设备台账设备
              let url = '/query/le/leAsset/findOne?id=' + text;
              this.httpService.get(url, null, res => {
                if (res.code === 0) {
                  if (res.data[0]['locationtype'] != 'system') {
                    this.navCtrl.push('LeassetinfoPage', { 'assetinfo': res.data[0] });
                  } else {
                    this.navCtrl.push('LeassetPage', { 'nodetypeCA': true, 'description': res.data[0].description, 'globalnum': res.data[0].gobalnum });
                  }
                }
              })
            }
            if (this.type === 'pitask') {//巡检任务单
              if ((this.platform.is('android') || this.platform.is('ios')) && this.platform.is('cordova') && this.offlinepi == 1) {
                this.sqlite.initDb(() => {
                  let query_findlastresult = "select * from pi_task_loc where taskid = ? and assetnum=?";
                  this.sqlite.queryOne(query_findlastresult, [this.taskid, text], result => {
                    if (result != null && result != undefined) {
                      this.navCtrl.push('PiTasklocPage', {
                        'taskid': result['taskid'],
                        'locid': result['locid'],
                        'tasklocid': result['tasklocid'],
                        'patrolmanname': this.navParams.get('patrolmanname'),
                        'offlienfrom': this.navParams.get('offlienfrom')
                      })
                    }
                  });
                });
              } else {
                this.httpService.get('query/pi/piTaskLoc/findTaskLoc?taskid=' + this.taskid + '&locationnum=' + text, null, res => {
                  if (res.code === 0) {
                    this.navCtrl.push('PiTasklocPage', {
                      'taskid': this.taskid,
                      'locid': res.data[0].locid,
                      'tasklocid': res.data[0].tasklocid,
                      'patrolmanname': this.navParams.get('patrolmanname'),
                      'offlienfrom': this.navParams.get('offlienfrom')
                    })
                  }
                })
              }
            }
            if (this.type === 'piinout') {//进出登记
              this.httpService.get('/piInOut/qrcodePiInOut?locationnum=' + text, null, res => {
                if (res.code === 0) {
                  this.navCtrl.push('PiinoutinfoPage', {
                    id: res.data[0].id
                  })
                }
              })
            }
            if (this.type === 'tmtool') {//工器具
              this.httpService.get('/query/tm/tmTool/findTmToolCode?code=' + text, null, res => {
                if (res.code === 0) {
                  this.navCtrl.push('TmtoolinfoPage', { 'title': res.data[0].description, 'tool': res.data[0] });
                }
              })
            }
            if (this.type === 'fmtoolplan') {//消防检验
              this.httpService.get('/query/fm/fmToolPlan/findFmToolPlanByCode?code=' + text, null, res => {
                if (res.code === 0) {
                  this.navCtrl.push('FmtoolplaninfoPage', { 'toolplaninfo': res.data[0] });
                }
              })
            }
            if (this.type === 'tmtoolplan') {//工器具检验
              this.httpService.get('/query/tm/tmToolPlan/findTmToolPlanByCode?code=' + text, null, res => {
                if (res.code === 0) {
                  this.navCtrl.push('TmtoolplaninfoPage', { 'toolplaninfo': res.data[0] });
                }
              })
            }
            if (this.type === 'tmToolInventory') {//工器具盘点
              this.httpService.get('/tmToolInventory/qrcodeTmTool?id=' + this.navParams.get('id') + '&toolcode=' + text, null, res => {
                if (res.code === 0) {
                  this.navCtrl.push('TmtoolinventorytabledetailsPage', {
                    id: res.data[0].id
                  })
                }
              })
            }
            if (this.type === 'tmToolRecord') {//工器具维修单
              // /tmToolRecord/qrcodeTmTool?id=维修单ID&toolcode=工器具编号
              // /tmToolRecord/qrcodeTmTool?id=维修单ID&toolcode=工器具编号
              this.httpService.get('/tmToolRecord/qrcodeTmTool?id=' + this.navParams.get('id') + '&toolcode=' + text, null, res => {
                if (res.code === 0) {
                  this.navCtrl.push('TmtoolrecordinfotabledetailsPage', {
                    id: res.data[0].id
                  })
                  // this.navCtrl.push('TmtoolinventorytabledetailsPage',{});
                }
              })
            }
            if (this.type === 'fmToolInventory') {//消防
              this.httpService.get('/fmToolInventory/qrcodeInventory?id=' + this.navParams.get('id') + '&code=' + text, null, res => {
                if (res.code === 0) {
                  this.navCtrl.push('FmtoolinventorytabledetailsPage', {
                    id: res.data[0].id
                  })
                  // this.navCtrl.push('TmtoolinventorytabledetailsPage',{});
                }
              })
            }
            if (this.type === 'fmToolInventory') {//消防
              this.httpService.get('/fmToolInventory/qrcodeInventory?id=' + this.navParams.get('id') + '&code=' + text, null, res => {
                if (res.code === 0) {
                  this.navCtrl.push('FmtoolinventorytabledetailsPage', {
                    id: res.data[0].id
                  })
                  // this.navCtrl.push('TmtoolinventorytabledetailsPage',{});
                }
              })
            }
            if (this.type === 'Emgtoolinventory') {//应急
              this.httpService.get('/emgSuppliesInventory/qrcodeInventory?id=' + this.navParams.get('id') + '&code=' + text, null, res => {
                if (res.code === 0) {
                  this.navCtrl.push('EmgtoolinventorytabledetailsPage', {
                    id: res.data[0].id
                  })
                  // this.navCtrl.push('TmtoolinventorytabledetailsPage',{});
                }
              })
            }
            if (this.type === 'EmgsuppliesPage') {//应急物资管理
              this.httpService.get('/query/emg/emgSupplies/findEmgSuppliesByCode?code=' + text, null, res => {
                if (res.code === 0) {
                  this.navCtrl.push('EmgsuppliesinfoPage', {
                    id: res.data[0].id,
                    type: 'edit'
                  })
                  // this.navCtrl.push('TmtoolinventorytabledetailsPage',{});
                }
              })
            }
            if (this.type === 'semInspectionTask') { // 特种设备检验
              this.httpService.get('/semInspectionTask/list?kksnum=' + text, null, res => {
                if (res.total > 0) {
                  this.navCtrl.push('SeminspectionrecordmanifestPage', {
                    taskid: res.rows[0].taskid,
                    type: 'edit'
                  })
                } else {
                  this.navCtrl.push('SeminspectionrecordmanifestPage', {
                    kksnum: text,
                    type: 'add'
                  })
                }
              })
            }

            // 其他
            const qrdata = JSON.parse(text);
            if (qrdata.type == 'lmtagQualityInspection') {
              setTimeout(() => {
                this.navCtrl.push('LmtagqualityinspectioninfoPage', {
                  type: 'edit',
                  id: qrdata.id
                })
              }, 1);
            }


            this.qrscanner.hide();
            scanSub.unsubscribe();
            this.qrscanner.destroy();
            this.navCtrl.pop();
          })
          // this.qrscanner.show();
        } else if (status.denied) {
          //提醒用户权限没有开启  
          // alert('没有权限开启')
          this.qrscanner.openSettings();
        } else {

        }
      }, (err) => {
        alert('出错了' + err);
      })
    } else {

    }

  }
  //手机手电筒控制
  openlight() {
    this.lightFlag = !this.lightFlag;
    if (this.lightFlag == true) {
      this.qrscanner.enableLight();
    } else {
      this.qrscanner.disableLight();
    }
  }
  /**
   * Resolves the QR code data and performs specific actions based on the type of QR code.
   * @param {string} text - The QR code text to be resolved.
   */
  resolveQrCodeData(text: string) {
    console.log(`resolveQrCodeData  text => `, text)
    if (this.type === 'leasset') {//设备台账设备
      let url = '/query/le/leAsset/findOne?id=' + text;
      this.httpService.get(url, null, res => {
        if (res.code === 0) {
          if (res.data[0]['locationtype'] != 'system') {
            this.navCtrl.push('LeassetinfoPage', { 'assetinfo': res.data[0] });
          } else {
            this.navCtrl.push('LeassetPage', { 'nodetypeCA': true, 'description': res.data[0].description, 'globalnum': res.data[0].gobalnum });
          }
        }
      })
    }
    if (this.type === 'pitask') {//巡检任务单
      if ((this.platform.is('android') || this.platform.is('ios')) && this.platform.is('cordova') && this.offlinepi == 1) {
        this.sqlite.initDb(() => {
          let query_findlastresult = "select * from pi_task_loc where taskid = ? and assetnum=?";
          this.sqlite.queryOne(query_findlastresult, [this.taskid, text], result => {
            if (result != null && result != undefined) {
              this.navCtrl.push('PiTasklocPage', {
                'taskid': result['taskid'],
                'locid': result['locid'],
                'tasklocid': result['tasklocid'],
                'patrolmanname': this.navParams.get('patrolmanname'),
                'offlienfrom': this.navParams.get('offlienfrom')
              })
            }
          });
        });
      } else {
        this.httpService.get('query/pi/piTaskLoc/findTaskLoc?taskid=' + this.taskid + '&locationnum=' + text, null, res => {
          if (res.code === 0) {
            this.navCtrl.push('PiTasklocPage', {
              'taskid': this.taskid,
              'locid': res.data[0].locid,
              'tasklocid': res.data[0].tasklocid,
              'patrolmanname': this.navParams.get('patrolmanname'),
              'offlienfrom': this.navParams.get('offlienfrom')
            })
          }
        })
      }
    }
    if (this.type === 'piinout') {//进出登记
      this.httpService.get('/piInOut/qrcodePiInOut?locationnum=' + text, null, res => {
        if (res.code === 0) {
          this.navCtrl.push('PiinoutinfoPage', {
            id: res.data[0].id
          })
        }
      })
    }
    if (this.type === 'tmtool') {//工器具
      this.httpService.get('/query/tm/tmTool/findTmToolCode?code=' + text, null, res => {
        if (res.code === 0) {
          this.navCtrl.push('TmtoolinfoPage', { 'title': res.data[0].description, 'tool': res.data[0] });
        }
      })
    }
    if (this.type === 'fmtoolplan') {//消防检验
      this.httpService.get('/query/fm/fmToolPlan/findFmToolPlanByCode?code=' + text, null, res => {
        if (res.code === 0) {
          this.navCtrl.push('FmtoolplaninfoPage', { 'toolplaninfo': res.data[0] });
        }
      })
    }
    if (this.type === 'tmtoolplan') {//工器具检验
      this.httpService.get('/query/tm/tmToolPlan/findTmToolPlanByCode?code=' + text, null, res => {
        if (res.code === 0) {
          this.navCtrl.push('TmtoolplaninfoPage', { 'toolplaninfo': res.data[0] });
        }
      })
    }
    if (this.type === 'tmToolInventory') {//工器具盘点
      this.httpService.get('/tmToolInventory/qrcodeTmTool?id=' + this.navParams.get('id') + '&toolcode=' + text, null, res => {
        if (res.code === 0) {
          this.navCtrl.push('TmtoolinventorytabledetailsPage', {
            id: res.data[0].id
          })
        }
      })
    }
    if (this.type === 'tmToolRecord') {//工器具维修单
      // /tmToolRecord/qrcodeTmTool?id=维修单ID&toolcode=工器具编号
      // /tmToolRecord/qrcodeTmTool?id=维修单ID&toolcode=工器具编号
      this.httpService.get('/tmToolRecord/qrcodeTmTool?id=' + this.navParams.get('id') + '&toolcode=' + text, null, res => {
        if (res.code === 0) {
          this.navCtrl.push('TmtoolrecordinfotabledetailsPage', {
            id: res.data[0].id
          })
          // this.navCtrl.push('TmtoolinventorytabledetailsPage',{});
        }
      })
    }
    if (this.type === 'fmToolInventory') {//消防
      this.httpService.get('/fmToolInventory/qrcodeInventory?id=' + this.navParams.get('id') + '&code=' + text, null, res => {
        if (res.code === 0) {
          this.navCtrl.push('FmtoolinventorytabledetailsPage', {
            id: res.data[0].id
          })
          // this.navCtrl.push('TmtoolinventorytabledetailsPage',{});
        }
      })
    }
    if (this.type === 'fmToolInventory') {//消防
      this.httpService.get('/fmToolInventory/qrcodeInventory?id=' + this.navParams.get('id') + '&code=' + text, null, res => {
        if (res.code === 0) {
          this.navCtrl.push('FmtoolinventorytabledetailsPage', {
            id: res.data[0].id
          })
          // this.navCtrl.push('TmtoolinventorytabledetailsPage',{});
        }
      })
    }
    if (this.type === 'Emgtoolinventory') {//应急
      this.httpService.get('/emgSuppliesInventory/qrcodeInventory?id=' + this.navParams.get('id') + '&code=' + text, null, res => {
        if (res.code === 0) {
          this.navCtrl.push('EmgtoolinventorytabledetailsPage', {
            id: res.data[0].id
          })
          // this.navCtrl.push('TmtoolinventorytabledetailsPage',{});
        }
      })
    }
    if (this.type === 'EmgsuppliesPage') {//应急物资管理
      this.httpService.get('/query/emg/emgSupplies/findEmgSuppliesByCode?code=' + text, null, res => {
        if (res.code === 0) {
          this.navCtrl.push('EmgsuppliesinfoPage', {
            id: res.data[0].id,
            type: 'edit'
          })
          // this.navCtrl.push('TmtoolinventorytabledetailsPage',{});
        }
      })
    }
    if (this.type === 'semInspectionTask') { // 特种设备检验
      this.httpService.get('/semInspectionTask/list?kksnum=' + text, null, res => {
        if (res.total > 0) {
          this.navCtrl.push('SeminspectionrecordmanifestPage', {
            taskid: res.rows[0].taskid,
            type: 'edit'
          })
        } else {
          this.navCtrl.push('SeminspectionrecordmanifestPage', {
            kksnum: text,
            type: 'add'
          })
        }
      })
    }

    // 其他
    const qrdata = JSON.parse(text);
    if (qrdata.type == 'lmtagQualityInspection') {
      setTimeout(() => {
        this.navCtrl.push('LmtagqualityinspectioninfoPage', {
          type: 'edit',
          id: qrdata.id
        })
      }, 1);
    }
  }

  /**
   * Scans for QR code using the browser's camera.
   * @return {void} This function does not return a value.
   */
  browserQRScanner() {
    if (Html5Qrcode) {
      this.loading = this.loadingCtrl.create({
        content: '智能扫码加载中'
      });
      this.loading.present();
      Html5Qrcode.getCameras()
        .then((devices) => {
          if (devices && devices.length) {
            let cameraId = "";
            if (devices.length == 1) {
              cameraId = devices[0].id; //前置摄像头
            } else {
              cameraId = devices[1].id;  //后置摄像头
            }
            if (cameraId) {
              this.startWithCameraId(cameraId);
            }
          } else {
            this.startWithoutCameraId();
          }
        })
        .catch((err) => {
          console.log("没有获取摄像头设备...");
          this.loading.dismiss();
          this.toastCtrl.create({
            message: '没有获取到摄像头设备,请重试',
            duration: 3000,
            position: 'top'
          }).present()
        });
    }
  }
  //带相机ID扫描
  startWithCameraId(cameraId) {
    this.qrCode = new Html5Qrcode('reader');
    this.qrCode.start(
        { deviceId: { exact: cameraId } },
        { fps: 10, qrbox: 220 },
        this.onScanSuccess, // 扫码成功回调函数
        this.onScanFailure
      )
      .then(() => {
        // 插件启动成功
          this.loading.dismiss();
      })
      .catch((err) => {
        console.log("通过摄像头扫码异常....", err);
        this.loading.dismiss();
        this.toastCtrl.create({
          message: '摄像头启动失败,请重试',
          duration: 3000,
          position: 'top'
        }).present()
      });
  }
  //不带相机ID扫描,允许传递约束来代替相机设备 ID
  startWithoutCameraId() {
    this.qrCode = new Html5Qrcode('reader');
    //environment 表示后置摄像头  换成user则表示前置摄像头
    this.qrCode.start(
      { facingMode: "environment" } || {
        facingMode: { exact: "environment" },
      },
      { fps: 10, qrbox: 220 },  // 设置每秒多少帧 , 设置取景范围
      this.onScanSuccess,
      this.onScanFailure
    )
    .then(() => {
      // 插件启动成功
      this.loading.dismiss();
    })
    .catch((err) => {
      console.log("通过摄像头扫码异常....", err);
      this.loading.dismiss();
      this.toastCtrl.create({
        message: '摄像头启动失败,请重试',
        duration: 3000,
        position: 'top'
      }).present()
    });
  }
  onScanSuccess(decodedText, decodedResult) {
    console.log(`onScanSuccess  decodedText => `, decodedText)
    if (decodedText != '' && decodedText != 'undefined') {
      this.onScanStop();
      this.resolveQrCodeData(decodedText);
    }
    // else {
    //   this.toastCtrl.create({
    //     message: '二维码无数据,请重新扫描',
    //     duration: 3000,
    //     position: 'top'
    //   }).present()
    // }
  }
  onScanFailure(error) {
    console.log(`onScanFailure  error => `, error)
  }
  onScanStop() {
    // this.qrCode.stop()
    //   .then((ignore) => {
    //     // QR Code scanning is stopped.
    //     console.log("QR Code scanning stopped.");
    //     this.qrCode.clear();
    //   })
    //   .catch((err) => {
    //     // Stop failed, handle it.
    //     console.log("Unable to stop scanning.");
    //   });
    this.qrCode.stop();
    this.qrCode.destroy();
  }
}



